import {
objectIdSchema,
propertyValueSchema,
selectionRectanglesSchema } from "./commonSchemas";

import { propertyTypes } from "../propertyTypes";

export const emitSchemaTypes = true;

export let SuggestionState;(function (SuggestionState) {SuggestionState["labelMatch"] = "Match / Label";SuggestionState["labelMismatch"] = "Mismatch / Label";SuggestionState["valueMatch"] = "Match / Value";SuggestionState["valueMismatch"] = "Mismatch / Value";SuggestionState["empty"] = "Empty / Empty";SuggestionState["obsolete"] = "Obsolete";SuggestionState["labelEmpty"] = "Empty / Label";SuggestionState["valueEmpty"] = "Empty / Value";SuggestionState["error"] = "Error";SuggestionState["processing"] = "Processing";SuggestionState["emptyMismatch"] = "Mismatch / Empty";})(SuggestionState || (SuggestionState = {}));













export const IXSuggestionSchema = {
  type: 'object',
  additionalProperties: false,
  title: 'IXSuggestionType',
  definitions: { objectIdSchema, propertyTypes, propertyValueSchema },
  properties: {
    _id: objectIdSchema,
    entityId: { type: 'string', minLength: 1 },
    fileId: objectIdSchema,
    propertyName: { type: 'string', minLength: 1 },
    suggestedValue: propertyValueSchema,
    suggestedText: { type: 'string' },
    segment: { type: 'string', minLength: 1 },
    language: { type: 'string', minLength: 1 },
    page: { type: 'number', minimum: 1 },
    status: { type: 'string', enum: ['processing', 'failed', 'ready'] },
    state: { type: 'string', enum: Object.values(SuggestionState) },
    date: { type: 'number' },
    error: { type: 'string' },
    selectionRectangles: selectionRectanglesSchema },

  required: ['propertyName', 'entityId', 'suggestedValue', 'segment', 'language'] };


